<template>
    <div class="marketingActivityAllocation">
        <div class="header">
            <el-input
                class="input"
                size="small"
                v-model.trim="queryForm.hdbq"
                placeholder="请输入活动标签"
            ></el-input>
            <el-input
                class="input"
                size="small"
                v-model.trim="queryForm.idtypeKey"
                placeholder="请输入营销活动内容关键词"
            ></el-input>
            <el-button size="small" type="primary" @click="getTableData">查询</el-button>
            <el-button size="small" type="primary" @click="clickAdd">新增营销活动</el-button>
        </div>
        <div class="content">
            <!-- <div class="content-title">
                <div class="content-title-left">
                  <span class="block"></span>
                  <span class="text">客户关注热点趋势分析</span>
                </div>
            </div>-->
            <el-table
                class="content-table"
                border
                :data="tableData"
                :height="500"
                :row-class-name="tableRowClassName"
            >
                <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
                <el-table-column prop="hdmc" label="营销活动名称" width="120"></el-table-column>
                <el-table-column prop="hdmd" label="营销活动目的"></el-table-column>
                <el-table-column prop="hdnr" label="营销活动内容" ></el-table-column>
                <el-table-column prop="yxhdbq" label="活动分类标签" ></el-table-column>
                <el-table-column prop="sjywj" label="设计源文件" width="120">
                    <template slot-scope="scope">
                        <span class="text" type="text" @click="importFile(scope.row.hdbh)">导出源文件</span>
                    </template>
                </el-table-column>
                <el-table-column prop="tplj" label="图片" width="100">
                    <template slot-scope="scope">
                        <span class="text" type="text" @click="importImg(scope.row.hdbh)">导出图片</span>
                    </template>
                </el-table-column>
                <el-table-column align="center" width="120" label="操作" >
                    <template slot-scope="scope">
                        <span class="text" type="text" @click="exportContent(scope.row.hdbh)">导出活动内容</span>
                    </template>
                </el-table-column>
                <el-table-column prop="czsj" label="创建时间" width="120"></el-table-column>
                <el-table-column prop="czr" label="创建人" width="120"></el-table-column>
            </el-table>
            <div class="content-page">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="this.queryForm.current"
                    :page-sizes="[10,20,30,40]"
                    :page-size="this.queryForm.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="this.queryForm.total"
                ></el-pagination>
            </div>
        </div>
        <el-dialog
            class="add-dialog"
            title="新增营销活动"
            :visible.sync="addDialogVisible"
            width="80%"
            fullscreen
            :before-close="addDialogClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '新增营销活动' }}</span>
      </span>

            <div class="dialog-pagination">
                <el-row :gutter="20" class="item">
                    <el-col :span="4">
                        <div class="label">活动名称 ：</div>
                    </el-col>
                    <el-col :span="18">
                        <el-input type="textarea" :rows="2" placeholder="请输入活动名称"
                                  v-model="addDialogForm.activeName"></el-input>
                    </el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="4">
                        <div class="label">活动目的 ：</div>
                    </el-col>
                    <el-col :span="18">
                        <el-input type="textarea" :rows="3" placeholder="请输入活动目的"
                                  v-model="addDialogForm.activeObjective"></el-input>
                    </el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="4">
                        <div class="label">活动内容 ：</div>
                    </el-col>
                    <el-col :span="18">
                        <el-input type="textarea" :rows="4" placeholder="请输入活动名称"
                                  v-model="addDialogForm.activeCon"></el-input>
                    </el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="4">
                        <div class="label">活动有效期选择 ：</div>
                    </el-col>
                    <el-col :span="18">
                        <el-date-picker
                            style="width: 100%"
                            v-model="addDialogForm.activeYxq"
                            type="date"
                            placeholder="选择日期"
                            format="yyyy 年 MM 月 dd 日"
                            value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="4">
                        <div class="label">分类标签选择 ：</div>
                    </el-col>
                    <el-col :span="18">
                        <div class="left">
                            <el-input
                                style="width: calc(100% - 66px)"
                                placeholder="输入关键字进行过滤"
                                size="medium"
                                v-model="filterText2">
                            </el-input>
                            <el-button @click="selectTree(filterText2)" icon="el-icon-search"
                                       size="medium"></el-button>
                            <!--                     default-expand-all-->
                            <el-tree
                                :style="treeStyleObject"
                                v-loading="treeLoading"
                                v-show="!showSelectTree"
                                class="filter-tree"
                                :filter-node-method="filterNode"
                                ref="tree"
                                :props="defaultProps"
                                :load="loadNode"
                                :highlight-current="true"
                                :expand-on-click-node="false"
                                @node-click="nodeClick"
                                lazy>
                                <span class="custom-tree-node" slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                            </el-tree>
                            <el-tree
                                :style="{treeStyleObject}"
                                v-loading="treeLoading"
                                v-show="showSelectTree"
                                :data="dataTree"
                                default-expand-all
                                :props="defaultProps2"
                                :expand-on-click-node="false"
                                @node-click="nodeClick"
                                :highlight-current="true">
                                <span class="custom-tree-node" slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                            </el-tree>
                        </div>
                        <div class="right">
                            <div class="title"><span style="margin-right: 20px">已选标签名称</span>  <el-button type="warning"  icon="el-icon-delete"  size="small" @click="delAllTag">全部清除</el-button></div>
                            <el-tag class="tag" v-for="item in tagSelectList" :key="item.tagId">{{item.tagName}}<span class="el-icon-close closeTag" @click="deleteTag(item)"></span></el-tag>
                        </div>
                    </el-col>
                </el-row>
                <div class="bottom">
                    <el-row :gutter="20" class="item">
                        <el-col :span="12">
                            <el-upload
                                class="upload-demo"
                                :action="uploadFileUrl"
                                :on-remove="handleRemove"
                                :on-success="handleAvatarSuccessFile"
                                multiple
                                :limit="1"
                                :on-exceed="handleExceed"
                                :file-list="fileList">
                                <el-button size="small" type="primary">上传设计源文件</el-button>
                                <!--                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
                            </el-upload>
                        </el-col>
                        <el-col :span="12">
                            <el-upload
                                class="upload-demo"
                                :action="uploadImgUrl"
                                :on-remove="handleRemove"
                                :on-success="handleAvatarSuccessImg"
                                multiple
                                :limit="1"
                                :on-exceed="handleExceed"
                                :file-list="imgList">
                                <el-button size="small" type="primary">上传活动图片</el-button>
                            </el-upload>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" type="primary" @click="addData">确认</el-button>
                <el-button size="small" type="warning" @click="addDialogClose">关闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/precision-marketing';
  import Interface2 from '@/base/mixin/common-api';
  import { CP_PATH,YXHD_TAG_OBJ} from '@/config'

  export default {
    name: "marketingActivityAllocation",
    components: {},
    mixins: [Interface,Interface2],
    props: {},
    data() {
      return {
        queryForm: {
          hdbq: "",
          idtypeKey: "",
          current: 1,
          size: 10,
          total: 0
        },
        // 表格
        tableData: [],
        // 新增框
        addDialogVisible: false,
        addDialogData: [],
        addDialogForm: {
          activeName: '',
          activeCon: '',
          activeObjective: '',
          activeYxq: '',
          current: 1,
          size: 10,
          total: 0
        },
        //标签树 新增短信模板相关
        defaultProps: {
          children: 'children',
          label: 'text',
          isLeaf: 'leaf'
        },
        defaultProps2: {
          children: 'children',
          label: 'text',
        },
        filterText2: '',
        treeLoading: false,
        showSelectTree: false,
        dataTree: [],

        node: [],
        resolve: [],
        numb: 1,
        treeStyleObject: {
          maxHeight: 400 + 'px',
          minHeight: 300 + 'px',
          overflow: 'scroll'
        },
        tagSelectList:[],
        // 文件上传
        imgList:[],
        uploadImgUrl:'',
        imgUrl:'',
        fileList:[],
        uploadFileUrl:'',
        fileUrl:''

      };
    },
    created() {

    },
    mounted() {
      this.uploadFileUrl =  CP_PATH +'/cptYxhdjbxx/uploadYFile'
      this.uploadImgUrl = CP_PATH +'/cptYxhdjbxx/uploadPhoto'
      this.getTableData()
    },
    methods: {
      //表格样式
      tableRowClassName({row, rowIndex}) {
        if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },
      // 分页
      handleSizeChange(val) {
        this.queryForm.size = val
        this.getTableData()
      },
      handleCurrentChange(val) {
        this.queryForm.current = val
        this.getTableData()
      },
      // 点击新增
      clickAdd(row) {
        this.addDialogVisible = true
      },
      // 新增框关闭
      addDialogClose() {
        this.addDialogVisible = false
      },
      // 新增框分页
      addDialogSizeChange(val) {
        this.addDialogForm.size = val
        this.getAddDialogData()
      },
      addDialogCurrentChange(val) {
        this.addDialogForm.current = val
        this.getAddDialogData()
      },
      // 导出活动模板
      exportContent(hdbh) {
        window.location.href=CP_PATH + '/cptYxhdjbxx/exportHdNr?hdbh='+hdbh
      },
      // 获取列表
      getTableData() {
        this.getXcyxActive({
          "hdbq": this.queryForm.hdbq,
          "hdnr": this.queryForm.idtypeKey,
          'size':this.queryForm.size,
          'current':this.queryForm.current,
        }).then((res) => {
          this.queryForm.total = res.total
          this.tableData = res.list
          window.console.log(res, 'ttt')
        })
      },
      // 获取新增列表
      getAddDialogData() {
      },

      //标签树
      selectTree(keywords) {
        this.treeLoading = true;
        let keywordTemp = ''
        if (keywords != '' && keywords != null) {
          keywordTemp = keywords
          this.showSelectTree = true
          this.value = ''
        } else {
          this.showSelectTree = false
          keywordTemp = this.value
          this.filterText2 = ''
        }
        this.node.childNodes = [];
        this.getBdaTagList({
          keyword: keywordTemp,
          id: 0,
        }).then(res => {
          this.dataTree = res
          this.treeLoading = false;
          this.resolve(this.dataTree)
        }).catch(() => {

        });
        //手动调用加载数据方法
        // this.loadNode(this.node, this.resolve);
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.text.indexOf(value) !== -1;
      },
      loadNode(node, resolve) {
        //加载数据的方法里把node，resolve存起来 ==== 只存初始化时的数据
        if (this.numb === 1) {
          this.node = node;
          this.resolve = resolve;
          // 把子节点清空，否则下次加载时会直接往里push子节点导致重复
          this.node.childNodes = [];
          this.numb++
        }
        // let permissionArr =[]
        // permissionArr = ['1','4']

        this.dataTree = []
        let $this = this
        if (node.data) {
          if (node.data.type) {
            this.getBdaTagList(
              {
                id: node.data.entryId,
                type: node.data.type,
                tagobjId:YXHD_TAG_OBJ,
                //filterStatus:[3,5,7], //[1,2,4,6],//过滤除了“入库3”、“停用5”、“下线7”的标签；
                // filterTagRuleTypes: [3, 4]//过滤其他标签，只要大数据平台  1==大数据平台，3==手工标记 ，4==人工智能平台
              }).then(res => {
              $this.dataTree = res
              $this.dataTree.forEach(item => {
                if (item.type == 'tag') {
                  item.leaf = true
                }
              })
              this.treeLoading =false;
              resolve($this.dataTree)
            }).catch(() => {

            });
          } else {
            resolve([])
          }

        } else {
          let params = {
            tagobjId:YXHD_TAG_OBJ,
            // keyword:'11',
            //filterStatus: ["7"],//过滤下线标签
            // filterTagRuleTypes: qs.stringify( permissionArr,{ arrayFormat: "repeat" })//过滤其他标签，只要手工标签
          };
          this.treeLoading =true;
          this.getBdaTagList(params).then(res => {
            $this.options = res
            for (let i = 0; i < res.length; i++) {
              res[i].disabled = true;
            }
            this.dataTree = res
            this.treeLoading =false;
            resolve(this.dataTree)
          }).catch(() => {

          });

        }
      },
      nodeClick(a) {
        if (a.type==='tag'){
          if (this.tagSelectList.length===0){
            this.tagSelectList.push({
              tagName:a.name,
              tagId:a.entryId,
            })
            return;
          }
          for (let i=0;i<this.tagSelectList.length;i++){
            if (this.tagSelectList[i].tagId===a.entryId){
              this.$message.success('当前标签以经存在')
              return;
            }
          }
          this.tagSelectList.push({
            tagName:a.name,
            tagId:a.entryId,
          })
        }
        // 这里有三个参数
      },
      //删除已选单个标签
      deleteTag(item){
        this.tagSelectList = this.tagSelectList.filter(items=>{
          if (items.tagId !== item.tagId){
            return items
          }
        })
      },
      //删除已选标签
      delAllTag(){
        this.tagSelectList = []
      },
      handleRemove(file, fileList) {
        window.console.log(file, fileList);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      handleAvatarSuccessFile(res, file) {
        this.fileUrl = file.response.data;
      },
      handleAvatarSuccessImg(res, file) {
        this.imgUrl = file.response.data;
      },
      addData(){
        if (this.tagSelectList.length<1){
          this.$message.warning('请选择标签')
          return
        }
        let tagIdArray = []
        let tagName = []
        for (let i=0;i<this.tagSelectList.length;i++){
          tagIdArray.push(this.tagSelectList[i].tagId)
          tagName.push(this.tagSelectList[i].tagName)
        }
        this.addXcyxActive({
          "hdmc":this.addDialogForm.activeName,
          "hdmd":this.addDialogForm.activeObjective,
          "hdnr":this.addDialogForm.activeCon,
          "hdyxq":this.addDialogForm.activeYxq,
          "yxhdbq":tagName.join(),
          "yxhdbqId":tagIdArray.join(),
          "sjywj":this.fileUrl,
          "tplj":this.imgUrl
        }).then((res)=>{
          this.$message.success(res)
          this.getTableData()
          this.addDialogVisible = false
        })
      },
      importFile(hdbh){
        window.location.href=CP_PATH + '/cptYxhdjbxx/downYFile?hdbh='+hdbh
      },
      importImg(hdbh){
        window.location.href=CP_PATH + '/cptYxhdjbxx/downPhoto?hdbh='+hdbh
      },
    }
  };
</script>

<style scoped lang="scss">
    .marketingActivityAllocation {
        ::v-deep .el-table .warning-row {
            // background: #e7f6f7;
            background: #e9f3fe;
        }

        ::v-deep .el-table {
            .success-row {
                background: rgb(244, 244, 244);
            }

            .cell {
                padding: 2px 4px;
            }

            th {
                background-color: rgb(24, 153, 250);

                .cell {
                    color: #ffffff;
                }
            }
        }

        .content-border {
            background: #ffffff;
            border: 2px solid rgba(26, 132, 244, 0.2);
            border-radius: 4px;
        }

        .header {
            height: 40px;
            display: flex;
            align-items: center;

            .select {
                margin-right: 20px;
            }

            .input {
                width: 300px;
                margin-right: 20px;
            }
        }

        .content {
            .content-table {
                width: 100%;
                // height: 350px;
                .text {
                    color: #1899fa;
                    cursor: pointer;
                }
            }

            .content-page {
                margin-top: 10px;
                width: 100%;
                text-align: center;
            }
        }

        .add-dialog {
            ::v-deep .el-table .warning-row {
                background: #e9f3fe;
            }

            ::v-deep .el-dialog__header {
                padding: 10px 0 10px;
                border-bottom: 1px solid #e6e6e6;
            }

            ::v-deep .el-dialog__body {
                padding: 10px;
            }

            ::v-deep .el-dialog__footer {
                padding: 10px 10px 10px 0;
                border-top: 1px solid #e6e6e6;
                text-align: center;
            }

            .dialog-header {
                .header-text {
                    margin-left: 20px;
                    color: #808080;
                    font-size: 14px;
                }
            }

            .query-group {
                margin-bottom: 10px;

                .search {
                    display: flex;
                    align-items: center;
                }

                .select {
                    margin-right: 20px;
                }

                .input {
                    width: 300px;
                    margin-right: 20px;
                }
            }

            .dialog-pagination {
                margin: 10px 0;
                .bottom{text-align: center}
                .item {
                    margin-bottom: 14px;

                    .label {
                        text-align: right;
                        margin-right: 6px;
                        line-height: 40px;
                    }

                    .left {
                        width: 380px;
                        display: inline-block;
                        min-height: 300px;
                        vertical-align: top;
                        margin-right: 20px;
                    }

                    .right {
                        display: inline-block;
                        min-height: 300px;
                        width: calc(100% - 400px);
                        vertical-align: top;
                        .tag{
                            min-width: 30px;
                            margin: 10px 8px;
                        }
                    }
                }
            }
        }
    }
</style>
